.scheduler {

  &__container {
    position: relative;
    display: flex;
    background-color: #E5E5E5;
  }

  &__dataView {
    position: relative;
    width: 70%;
    padding: 0 30px 48px;
    box-sizing: border-box;
  }

  &__form {
    width: 30%;
    height: 100vh;
    position: fixed;
    overflow-y: scroll;
    right: 0;
    top: 0;
    background-color: white;
  }
}